* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: purple;
    background-image: url(./images/backgroundImage.jpg);
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}
.Nav {
    max-height: 15rem;
    max-width: 100%;
    display: flex;
}
.nav_Img {
    width: 100%;
}
.navlink {
    color: white;
    text-decoration: none;
    display: flex;
    padding: 1% 5%;
}
.linksection {
    display: flex;
    background-color: rgba(65, 198, 255, 0.761);
}
.Cards {
    background-color: #000;
    display: flex;
    justify-content: space-between;
}
.card {
    margin: 2rem;
    border-radius: 2rem;
    background-color: white;
    max-width: 400px;

    min-height: 400px;
}
.img {
    padding-top: 3rem;
    max-width: 20rem;
}
.uphome {
    background-color: #000;
    width: 80%;
    margin-left: 10%;
    margin-top: 2rem;
}
.scard_Design {
    margin: 10px 10px 10px 0px;
    display: flex;
    color: aliceblue;
}
.s_Img {
    height: 60px;
    width: 60px;
    margin-right: 10px;
}
.sections_top {
    color: aliceblue;
    display: flex;
    max-width: 100%;
    padding: 0 2rem;
    flex-flow: row;
}
.sections_bottom {
    color: aliceblue;
    display: flex;
    max-width: 100%;
    padding: 2rem;
}
.section_Img {
    width: 100%;
}
.section1,
.section2,
.section3,
.section4 {
    padding: 1rem;
    min-width: 33%;
    float: left;
}
.section_Heading:hover {
    color: rgb(0, 208, 255);
}
.footer {
    padding: 2rem;
    display: flex;
    color: #fff;
    background-color: rgba(188, 171, 171, 0.307);
    margin-bottom: 2rem;
}
.footer_l {
    flex-flow: column;
}
.footer_l,
.footer_m,
.footer_r {
    width: 33%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.footer_Img {
    height: 12rem;
    border-radius: 100%;
}
.left {
    display: flex;
    max-width: 100%;
    float: left;
    flex-flow: row;
}
.section_left {
    max-width: 75%;
}
.section_right {
    display: flex;
    flex-flow: column;
    max-width: 25%;
}
.socials {
    margin: 1rem 0;
}
.socialhandles {
    padding: 0.8rem;
    border: solid 0.6rem rgba(65, 198, 255, 0.761);
}
.stayconnected_Img {
    width: 100%;
    border-radius: 2rem;
}
.stay_connected {
    margin: 1.3rem 0;
}
